<template lang="">
  <div id="pagination">
    <div class="btn-group my_btn_group">
      <button
        type="button"
        class="btn border-0"
        :disabled="page == 1"
        @click="$emit('firstPage')"
      >
        <i class="fa-solid fa-chevron-left fa-lg"></i>
      </button>
      <button
        type="button"
        class="btn btn-sm back"
        :class="btnColor"
        :disabled="page == 1"
        @click="$emit('pagPrec')"
      >
        Previous
      </button>
      <button
        :style="btnStyle"
        type="button"
        class="btn btn-md current"
        :class="btnPageColor"
        v-for="pageNumber in pages.slice(page - 1, page + 2)"
        :key="pageNumber"
        @click="$emit('currentPage', pageNumber)"
      >
        {{ pageNumber }}
      </button>
      <button
        type="button"
        class="btn btn-md next"
        :class="btnColor"
        :disabled="page == pages.length"
        @click="$emit('pagSucc')"
      >
        Next
      </button>
      <button
        type="button"
        class="btn border-0"
        :disabled="page == pages.length"
        @click="$emit('lastPage')"
      >
        <i class="fa-solid fa-chevron-right fa-lg"></i>
      </button>
    </div>
  </div>
</template>
<script>
export default {
  name: "PaginationComp",
  data() {
    return {};
  },
  props: {
    btnColor: String,
    btnPageColor: String,
    pageNumber: Number,
    pages: Array,
    page: Number,
    perPage: Number,
    btnStyle: Object
  },
};
</script>
<style lang="scss" scoped>
#pagination {
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;

  .my_btn_group {
    display: flex;
    align-items: center;

    .back,
    .current,
    .next {
      height: 40px;
    }

    .back,
    .next {
      min-width: 140px;
      font-size: 0.9rem;

      @media all and (max-width: 600px) {
        min-width: 60px;
      }
    }
    .current {
      min-width: 50px;

      @media all and (max-width: 600px) {
        min-width: 40px;
      }
    }
  }
}</style>
