<template>
  <div id="table">
    <!-- <button @click="prev" :disabled="min === 0">prev</button>
    <button @click="next" :disabled="max >= this.searchResults.length">
      next
    </button>
    <div :key="searchResult.trackId" v-for="searchResult in list">
      <SearchItem :searchResult="searchResult" />
    </div> -->
    <Pagination
      :btn-color="btnColor"
      :btn-page-color="btnPageColor"
      v-if="searchResults.length > 0"
      :page-number="pageNumber"
      :pages="pages"
      :page="page"
      :perPage="perPage"
      @pagPrec="pagPrec"
      @pagSucc="pagSucc"
      @firstPage="firstPage"
      @lastPage="lastPage"
      @currentPage="currentPage"
      @setmainList="setmainList"
      :paginateList="paginateList"
    />
    <div class="container">
      <div class="row">
        <div class="dashboard col">
          <div
            class="card"
            :key="searchResult.trackId"
            v-for="(searchResult, index) in displayedMainList"
          >
            <SearchItem :searchResult="searchResult" :my-id="index"/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchItem from "./SearchItem.vue";
import Pagination from "@/components/SharedComponent/Pagination.vue";

export default {
  name: "TableComp",
  props: {
    searchResults: Array,
  },
  components: {
    SearchItem,
    Pagination,
  },
  data() {
    return {
      // perPage: 5,
      // currentPage: 1,
      // totalPages: 0,
      // min: 0,
      // max: 5,

      btnColor: "btn-warning",
      btnPageColor: "btn-outline-warning",
      pageNumber: 0,
      pages: [],
      page: 0,
      perPage: 10,
    };
  },
  computed: {
    displayedMainList() {
      return this.paginateList(this.searchResults);
    },
  },
  watch: {
    searchResults() {
      this.setmainList();
      this.firstPage();
    },
  },
  methods: {
    // PAGINATION
    pagPrec() {
      this.page--;
    },
    pagSucc() {
      this.page++;
    },
    firstPage() {
      this.currentPage(this.pages[0]);
    },
    lastPage() {
      this.currentPage(this.pages[this.pages.length - 1]);
    },
    currentPage(page) {
      this.page = page;
    },
    setmainList() {
      this.pages = [];
      const numberOfPages = Math.ceil(this.searchResults.length / this.perPage);
      for (let i = 1; i <= numberOfPages; i++) {
        this.pages.push(i);
      }
      // console.log("pages " + this.pages);
    },
    paginateList(mainList) {
      const page = this.page;
      const perPage = this.perPage;
      const from = page * perPage - perPage;
      const to = page * perPage;
      return mainList.slice(from, to);
    },
  },
};
</script>

<style lang="scss" scoped>
#table {
  background: #000000;
  padding-bottom: 150px;
  @media all and (max-width: 992px) {
    padding-bottom: 50px;
  }
  .dashboard {
    // min-height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    .card {
      background: #000000;
      position: relative;
    }
  }
}
</style>
