<template>
  <div id="mbg">
    <Header 
      :img-src="require(`../assets/img/mbg/${myImg}.png`)"
      :img-alt="myImgAlt"
      :home="home"
    />
    <Form 
      :searchResults="searchResults" 
      @set-search="setSearch" 
    />
    <Table 
      :searchResults="searchResults"
    />
  </div>
</template>

<script>
import Header from '@/components/Mbg/Header.vue'
import Form from '@/components/Mbg/Form'
import Table from '@/components/Mbg/Table'

export default {
  name: 'ItunesView',
  components: {
    Header,
    Form,
    Table
  },
  data() {
    return {
      searchResults: [],
      myImg: "mbg",
      myImgAlt: "MBG",
      welcome: true,
    }
  },
  methods: {
    setSearch(data) {
      this.searchResults = data;
    },
    home() {
      this.reset();
    },
  },
  mounted() {
    window.scrollTo(0, 0);
  },
}
</script>
<style lang="scss" scoped>
  #mbg {
    background: #000;
    min-height: 100vh;
    // margin-bottom: 150px;
  }
</style>