<template>
  <div>
    <div class="backHome" @click="reset()"></div>
    <form id="form" @submit.prevent="onSubmit">
      <!-- <input
        type="text"
        v-model="searchTerm"
        name="searchTerm"
        placeholder="Search..."
      />
      <input type="submit" value="Submit" /> -->
      <div class="input-group">
        <label class="input-group__label" for="myInput">My Song</label>
        <input
          type="text"
          name="searchTerm"
          id="myInput"
          class="input-group__input"
          placeholder="Search..."
          v-model="searchTerm"
        />
      </div>
    </form>
    <div id="welcome" v-if="welcome">
      <img src="../../assets//img/mbg/cassette.gif" alt="mbg" />
    </div>
    <div id="overlay" v-if="loading">
      <breeding-rhombus-spinner
        :animation-duration="2000"
        :size="120"
        color="#ff1d5e"
      />
    </div>
    <div v-if="notFound" id="notFound">
      <h3>{{ this.query }}</h3>
      <h4>No Results Found</h4>
    </div>
  </div>
</template>

<script>
// import {eventBus} from "@/main";
import { BreedingRhombusSpinner } from "epic-spinners";

export default {
  name: "FormComp",
  // props: ["searchResults"],
  props: {
    searchResults: Array,
  },
  components: {
    BreedingRhombusSpinner,
  },
  data() {
    return {
      searchTerm: "",
      query: "",
      limit: 50,
      loading: false,
      notFound: false,
      welcome: true,
      emptyArray: [],
    };
  },
  mounted() {
    this.reset();
  },
  methods: {
    onSubmit() {
      // e.preventDefault();
      this.welcome = false;
      this.notFound = false;
      this.loading = true;
      setTimeout(() => {
      this.query = this.searchTerm;
      fetch(
        `https://itunes.apple.com/search?term=${this.searchTerm}&limit=${this.limit}`
        )
        .then((res) => res.json())
        .then((data) => {
          // console.log(data.results);
          let songs = data.results
          .filter((song) => song.kind === "song")
          .map((song) => this.extractData(song));
          // console.log(songs);
            this.loading = false;
            this.$emit("set-search", songs);
            if (songs.length <= 0) {
              this.notFound = true;
            }
          });
          this.searchTerm = "";
        }, "1000");
    },
    extractData({
      trackId: id,
      artistName: artist,
      previewUrl: audioFile,
      artworkUrl100: cover,
      trackName: name,
      collectionName: album,
      primaryGenreName: genre,
      trackViewUrl: tracksUrl,
      releaseDate: date,
    }) {
      return {
        id,
        artist,
        audioFile,
        cover,
        name,
        album,
        genre,
        tracksUrl,
        date,
      };
    },
    reset() {
      this.loading = false;
      this.notFound = false;
      this.welcome = true;
      this.$emit("set-search", this.emptyArray);
    },
  },
};
</script>

<style lang="scss" scoped>
.backHome {
  position: absolute;
  top: 0;
  height: 60px;
  width: 100%;
  background: transparent;
  border-bottom: 1px solid rgba(177, 161, 161, 0.164);
  @media all and (max-width: 992px) {
    left: 50%;
    transform: translateX(-50%);
  }
  &:hover {
    cursor: pointer;
  }
}
#form {
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  .input-group {
    width: 100%;
    max-width: 20em;
    display: flex;
    flex-direction: column;
    z-index: 2;

    @supports (mix-blend-mode: darken) {
      position: relative;
      mix-blend-mode: lighten;

      &__label {
        position: absolute;
        left: 3em;
        top: -0.28em;
        background: #000;
      }
    }

    &__label {
      padding: 0 0.5em;
      margin-bottom: 0.5em;
      text-transform: uppercase;
      font-size: 0.875em;
      letter-spacing: 0.1em;
      color: #ccd;
      color: rgba(255, 220, 255, 0.6);
      cursor: pointer;
    }

    &__input {
      color: #fff;
      font-size: 1.25rem;
      line-height: 1;
      border-style: none;
      outline: none;
      // height calc line-height + (vertical-padding * 2) + (vertical-border * 2)
      height: calc(1em + 1.6em + 0.5em);
      width: 100%;
      padding: 0.8em 1em;
      border: 0.25em solid transparent;
      background-image: linear-gradient(#000, #000),
        linear-gradient(120deg, #f09 0%, #0ff 50%, #9f0 100%);
      background-origin: border-box;
      background-clip: padding-box, border-box;
      border-radius: 1.8em;
      background-size: 200% 100%;
      transition: background-position 0.8s ease-out;

      &:hover {
        background-position: 100% 0;
      }

      &:focus {
        // outline: 2px dashed #ad2b89;
        outline-offset: 0.5em;
      }
    }
  }
}
#overlay {
  height: 300px;
  width: 300px;
  z-index: 998;
  position: absolute;
  background: rgba(0, 0, 0, 0.9);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
#welcome,
#notFound {
  height: 70vh;
  // width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #000000;
  color: #ff1d5e;
  h3 {
    font-size: 3rem;
  }
  h4 {
    font-size: 2.8rem;
  }
  img {
    height: 50%;
  }
}
#welcome {
  @media all and (max-width: 992px) {
    // justify-content: flex-start;
    padding-bottom: 50px;
  }
}
</style>
