<template>
  <div class="item">
    <div class="top">
      <span
        class="genre"
        v-text="substringText(searchResult.genre, 0, 20)"
      ></span>
      <img
        class="img-fluid"
        :src="imgFunction"
        :alt="searchResult.album"
        @load="onImgLoad"
      />
      <spring-spinner
        class="spinner"
        v-if="!this.isLoaded"
        :animation-duration="3000"
        color="#ff1d5e"
        :size="30"
        style="position: absolute;"
      />
    </div>
    <div class="bottom">
      <span class="artist">{{
        substringText(searchResult.artist, 0, 30)
      }}</span>
      <span
        class="title"
        v-text="substringText(searchResult.name, 0, 30)"
      ></span>
      <span
        class="album"
        v-text="substringText(searchResult.album, 0, 50)"
      ></span>
      <span class="date" v-text="dateYear(searchResult.date)"></span>
    </div>
    <div class="overlay" @click="play">
      <div class="audio">
        <i
          v-if="!playing"
          ref="controlElm"
          class="controls fa-solid fa-play fa-2xl"
        >
        </i>
        <i v-else ref="controlElm" class="controls fa-solid fa-pause fa-2xl">
        </i>
        <audio
          ref="audioElm"
          class="player"
          :myId="myId"
          :src="searchResult.audioFile"
        ></audio>
      </div>
    </div>
  </div>
</template>

<script>
import { SpringSpinner } from "epic-spinners";
export default {
  name: "SearchItemComp",
  props: {
    searchResult: Object,
    myId: Number,
  },
  components: {
    SpringSpinner,
  },
  data() {
    return {
      countDown: null,
      timeoutId: undefined,
      arrayOfSetTimeouts: [],
      isLoaded: false,
      playing: false,
    };
  },
  computed: {
    imgFunction() {
      if (this.searchResult.cover === null) {
        return require("@/assets/img/mbg/noimage.png");
      } else {
        return this.searchResult.cover;
      }
    },
  },
  watch: {
    searchResult() {
      this.playing = false;
      if (this.arrayOfSetTimeouts.length > 0) {
        this.myTimeoutStop();
      }
    },
    countDown() {
      if (this.countDown > 0) {
        this.timeoutId = setTimeout(() => {
          this.countDown--;
          // console.log(this.countDown);
        }, 1000);
        this.arrayOfSetTimeouts.push(this.timeoutId);
      } else {
        var controlElm = this.$refs.controlElm;
        controlElm.classList.remove("fa-pause");
        controlElm.classList.add("fa-play");
      }
    },
  },
  methods: {
    onImgLoad() {
      this.isLoaded = true;
    },
    substringText(string, min, max) {
      if (string.length > max) {
        return string.substring(min, max) + "...";
      } else {
        return string;
      }
    },
    dateYear(date) {
      var d = new Date(date);
      return d.getFullYear();
    },
    play() {
      this.myTimeoutStop();
      var audioElm = this.$refs.audioElm;
      var controlElm = this.$refs.controlElm;
      var audios = document.getElementsByTagName("audio");
      // console.log(audios);
      if (audioElm.paused) {
        for (var i = 0, len = audios.length; i < len; i++) {
          audios[i].pause();
          var parent = audios[i].parentNode;
          // console.log(parent); // id audio
          var firstChild = parent.firstElementChild;
          // console.log(control); // id controls
          firstChild.classList.remove("fa-pause");
          firstChild.classList.add("fa-play");
        }
        // console.log("on play");
        audioElm.play();
        this.playing = true;
        this.countDown = audioElm.duration;
        controlElm.classList.remove("fa-play");
        controlElm.classList.add("fa-pause");
      } else {
        // console.log("on pause");
        audioElm.pause();
        this.playing = false;
        controlElm.classList.remove("fa-pause");
        controlElm.classList.add("fa-play");
      }
    },
    myTimeoutStop() {
      // console.log(this.arrayOfSetTimeouts);
      if (this.arrayOfSetTimeouts.length > 0) {
        for (let id in this.arrayOfSetTimeouts) {
          clearTimeout(this.arrayOfSetTimeouts[id]);
        }
        this.arrayOfSetTimeouts = []; //clear IDs array
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.item {
  background: #1d1c1c79;
  padding: 5px;
  width: 200px;
  height: 250px;
  margin: 20px;
  border-radius: 9px;
  display: flex;
  flex-direction: column;
  box-shadow: rgba(223, 6, 205, 0.3) 0px 1px 2px 2px,
    rgba(6, 176, 182, 0.15) 0px 1px 3px 1px;
  transition: all 0.3s ease-in-out;

  @media all and (max-width: 600px) {
    width: 80vw;
    height: 50px;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
  }

  &:hover {
    box-shadow: rgba(245, 7, 185, 0.808) 0px 0px 26px,
      rgba(8, 230, 230, 0.23) 0px 6px 6px;
    cursor: pointer;

    .bottom {
      .date {
        display: none;
      }
    }
  }

  .top,
  .bottom {
    height: 50%;

    @media all and (max-width: 600px) {
      height: 100%;
    }
  }

  .top {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;

    @media all and (max-width: 600px) {
      align-items: flex-end;
      width: 30%;
    }

    .genre {
      font-size: 0.7rem;
      text-align: center;
      margin: 0 auto;
      margin-bottom: 5px;
      color: #ececec88;

      // background: rgb(197, 9, 50);
      @media all and (max-width: 600px) {
        display: none;
      }
    }

    img {
      height: 100%;
      border-radius: 50%;
      box-shadow: 0 0 0 3px #ffc10791;

      @media all and (max-width: 600px) {
        border-radius: 0;
        box-shadow: 0 0 0 3px transparent;
      }
    }
    .spinner {
      top: 50%;
      @media all and (max-width: 600px) {
        top: 12%;
        right: 3%
      }
    }
  }

  .bottom {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    color: #cecece;
    font-size: 1rem;
    font-weight: 700;
    text-transform: capitalize;

    @media all and (max-width: 600px) {
      align-items: flex-start;
      justify-content: flex-end;
      width: 70%;
    }

    .artist {
      text-transform: uppercase;
      font-size: 0.9rem;
      letter-spacing: 0.5px;
      overflow: hidden;
      text-overflow: ellipsis;

      @media all and (max-width: 600px) {
        font-size: 0.8rem;
      }
    }

    .title {
      @media all and (max-width: 600px) {
        font-size: 0.9rem;
      }
    }

    .album {
      font-size: 0.8rem;
      color: rgba(190, 183, 183, 0.5);
      overflow: hidden;
      text-overflow: ellipsis;

      @media all and (max-width: 600px) {
        display: none;
      }
    }

    .date {
      position: absolute;
      border: 1px dotted transparent;
      border-radius: 5px;
      font-size: 0.7rem;
      padding: 2px;
      width: 50px;
      text-align: center;
      color: white;
      font-weight: 900;
      top: 10px;
      left: 0;
      background: rgb(197, 9, 50);

      @media all and (max-width: 600px) {
        top: 0;
      }
    }
  }

  .overlay {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    border-radius: 9px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0);
    -webkit-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    -o-transition: opacity 1s ease-in-out;
    transition: opacity 1.8s ease-in-out;

    @media all and (max-width: 600px) {
      width: 100%;
      height: 60px;
      // background: blue;
      position: absolute;
      top: 15px;
    }

    &:hover {
      cursor: pointer;
      opacity: 1;
      background: rgb(2, 0, 36);
      // margin: 17px;
      background: linear-gradient(
        36deg,
        rgba(2, 0, 36, 1) 0%,
        rgba(223, 6, 205, 0.8) 35%,
        rgba(0, 212, 255, 1) 100%
      );
      filter: brightness(1.4) opacity(0.9);

      .audio {
        display: block;
      }
    }

    .audio {
      display: none;
      color: #ececec;
      font-weight: 900;
      font-size: 2rem;
      text-shadow: 1px 1px 1px #000, 3px 3px 5px rgb(0, 0, 0);
    }

    audio {
      filter: sepia(20%) saturate(70%) grayscale(1) contrast(99%) invert(12%);
      width: 200px;
      height: 25px;
    }
  }
}
</style>
